#buttons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

#content {
    display: flex;
    justify-content: center;
    margin: 2vmin;
}

#item {
    max-height: 45vmin;
    max-width: 90%;
    border-radius: 10px;
}
/* LIKE, SKIP, AND DISLIKE BUTTONS */

#likeButton {
    font-family:  'Bebas Neue', sans-serif;
    font-size:  5vmin;
    letter-spacing: 1px;
    font-weight: bold;
    color: white;
    border: solid;
    border-color: rgb(139, 196, 218);
    border-radius: 10px;
    background-color: rgb(139, 196, 218);
    padding: .5em;
}

#likeButton:hover {
    color:rgb(139, 196, 218);
    background-color: rgb(139, 196, 218, 0);
}

#skipButton {
    font-family:  'Bebas Neue', sans-serif;
    font-size:  5vmin;
    letter-spacing: 1px;
    font-weight: bold;
    color: white;
    border: solid;
    border-color: rgb(200, 200, 200);
    border-radius: 10px;
    background-color: rgb(200, 200, 200);
    padding: .5em;
}

#skipButton:hover {
    color:rgb(200, 200, 200);
    background-color: rgb(200, 200, 200, 0);
    
}

#dislikeButton {
    font-family:  'Bebas Neue', sans-serif;
    font-size:  5vmin;
    letter-spacing: 1px;
    font-weight: bold;
    color: white;
    border: solid;
    border-color: rgb(255, 203, 212);
    border-radius: 10px;
    background-color: rgb(255, 203, 212);
    padding: .5em;
}

#dislikeButton:hover {
    color:rgb(255, 203, 212);
    background-color: rgb(255, 203, 212, 0);
}